<template>
  <div class="home-container">
    <div class="particles-container">
      <Particles
        id="tsparticles"
        :particlesInit="particlesInit"
        :particlesLoaded="particlesLoaded"
        :options="particlesOptions"
      />
    </div>
    
    <div class="content animate__animated animate__fadeIn">
      <div class="profile-image">
        <div ref="container" class="model-container">
          <!-- 3D -->
        </div>
      </div>
      
      <h1 class="animate__animated animate__fadeInUp">Java <span class="highlight"></span></h1>
      <h2 class="animate__animated animate__fadeInUp animate__delay-1s"> |  | </h2>
      
      <div class="typing-container animate__animated animate__fadeInUp animate__delay-2s">
        <span class="typing-text">{{ typedText }}</span><span class="cursor">|</span>
      </div>
      
      <div class="cta-buttons animate__animated animate__fadeInUp animate__delay-3s">
        <button class="nav-btn projects-btn" @click="$router.push('/projects')"><span class="btn-icon"></span></button>
        <button class="nav-btn blog-btn" @click="$router.push('/blog')"><span class="btn-icon"></span></button>
        <button class="nav-btn about-btn" @click="$router.push('/about')"><span class="btn-icon"></span></button>
        <button class="nav-btn contact-btn" @click="$router.push('/contact')"><span class="btn-icon"></span></button>
      </div>
      
      <div class="tech-stack animate__animated animate__fadeInUp animate__delay-4s">
        <div v-for="(tech, index) in techStack" :key="index" class="tech-item">
          <div class="tech-icon" :class="tech.icon"></div>
          <span>{{ tech.name }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup name="HomePage">
import { ref, onMounted, onUnmounted } from 'vue';
import { loadSlim } from 'tsparticles-slim';
import * as THREE from 'three';
import { useParallax } from '@vueuse/core';

// 3D
let scene, camera, renderer, cube;
const container = ref(null);

const initThreeJS = () => {
  scene = new THREE.Scene();
  camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
  renderer = new THREE.WebGLRenderer({ alpha: true });
  renderer.setSize(300, 300);
  container.value.appendChild(renderer.domElement);

  const geometry = new THREE.BoxGeometry();
  const material = new THREE.MeshPhongMaterial({ color: 0x4d91ff });
  cube = new THREE.Mesh(geometry, material);
  
  const light = new THREE.DirectionalLight(0xffffff, 1);
  light.position.set(1, 1, 1);
  scene.add(light);
  
  scene.add(cube);
  camera.position.z = 5;
  
  animate();
};

const animate = () => {
  requestAnimationFrame(animate);
  cube.rotation.x += 0.01;
  cube.rotation.y += 0.01;
  renderer.render(scene, camera);
};

// 
useParallax(container);

// 
const phrases = [
  'Java',
  'Spring Boot',
  '',
  ''
];

const typedText = ref('');
let currentPhraseIndex = 0;
let currentCharIndex = 0;
let typingInterval;
let isDeleting = false;
let typingSpeed = 100;

// 
const typeText = () => {
  const currentPhrase = phrases[currentPhraseIndex];
  
  if (isDeleting) {
    typedText.value = currentPhrase.substring(0, currentCharIndex - 1);
    currentCharIndex--;
    typingSpeed = 50;
  } else {
    typedText.value = currentPhrase.substring(0, currentCharIndex + 1);
    currentCharIndex++;
    typingSpeed = 150;
  }
  
  if (!isDeleting && currentCharIndex === currentPhrase.length) {
    isDeleting = true;
    typingSpeed = 1500;
  } else if (isDeleting && currentCharIndex === 0) {
    isDeleting = false;
    currentPhraseIndex = (currentPhraseIndex + 1) % phrases.length;
    typingSpeed = 500;
  }
  
  typingInterval = setTimeout(typeText, typingSpeed);
};

// Particles
const particlesInit = async engine => {
  await loadSlim(engine);
};

const particlesLoaded = async container => {
  console.log('Particles container loaded', container);
};

// 
const techStack = [
  { name: 'Java', icon: 'java-icon' },
  { name: 'Spring', icon: 'spring-icon' },
  { name: 'MySQL', icon: 'mysql-icon' },
  { name: 'Redis', icon: 'redis-icon' },
  { name: 'Docker', icon: 'docker-icon' },
  { name: 'Kubernetes', icon: 'k8s-icon' }
];

// 
const particlesOptions = {
  background: {
    color: {
      value: '#0d1117'
    }
  },
  fpsLimit: 60,
  particles: {
    color: {
      value: '#4d91ff'
    },
    links: {
      color: '#4d91ff',
      distance: 150,
      enable: true,
      opacity: 0.5,
      width: 1
    },
    move: {
      enable: true,
      speed: 1,
      direction: 'none',
      random: false,
      straight: false,
      outMode: 'bounce',
      attract: {
        enable: false,
        rotateX: 600,
        rotateY: 1200
      }
    },
    number: {
      density: {
        enable: true,
        area: 800
      },
      value: 80
    },
    opacity: {
      value: 0.5
    },
    shape: {
      type: 'circle'
    },
    size: {
      random: true,
      value: 3
    }
  },
  detectRetina: true
};

onMounted(() => {
  // 3D
  initThreeJS();
  
  // 
  setTimeout(() => {
    typeText();
  }, 3000);
});

onUnmounted(() => {
  clearTimeout(typingInterval);
  // 3D
  if (renderer) {
    renderer.dispose();
  }
});
</script>

